export function getMsgToolTip(id_msg) {
    let _msg = ''
    if (id_msg == 1){ //Risco de Frustrao
        _msg = "Risco de Frustrao  uma medida de risco que indica o quanto o Benefcio Estimado % do Salrio est distante do salrio atual. Risco ALTO indica situao delicada e sugere AUMENTO da contribuio atual, e/ou APORTE, e/ou AUMENTO na Idade de Aposentadoria."
    }
    return _msg
}    

